import Master from "../components/Master";
import fetch from 'isomorphic-unfetch'
import Precio from "../components/Precio";
import Noticias from "../components/Noticias";

const Index = (props) => (
  <div>
    <Master>
      <div className="row">
        <div className="col-12">
          <h2>Precio del bitcoin</h2>
          <Precio precio={props.precioBitcoin}></Precio>
        </div>
        <div className="col-md-8">
          <h2>Noticias sobre el bitcoin</h2>
          {
            <Noticias noticias={props.noticias}></Noticias>
          }
        </div>
        <div className="col-md-4">
          <h2>Proximos eventos bitcoin</h2>
          {console.log(props.eventos)}
        </div>
      </div>
    </Master>
  </div>
);
Index.getInitialProps = async () => {
  const precio = await fetch('https://api.coinmarketcap.com/v2/ticker/1/');
  const noticias = await fetch('https://newsapi.org/v2/everything?q=bitcoin&sortBy=publishedAt&apiKey=63a7261d98dc44189cc0a36d04979555&language=es');
  const eventos = await fetch('https://www.eventbriteapi.com/v3/events/search/?q=Bitcoin&token=N4HFLRAYHZ26O2ZD3X6T');
  const resPrecio = await precio.json();
  const resNoticias = await noticias.json();
  const resEventos = await eventos.json();
  return {
    precioBitcoin: resPrecio.data.quotes.USD,
    noticias: resNoticias.articles,
    eventos: resEventos.events
  }
}
export default Index;
